.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
h2{
  font-family: Poppins; font-size: 50px;
}
.wrapper{
  display: flex; justify-content: space-between; align-items: center; flex-direction: column;
  background-color: #ffebeb; width: 100vw; height: 100vh;
}
.components{
  text-align: center; display: flex; flex-direction: column;
  justify-content: space-between; align-items: center;
}
.line-below{
  background-color: rgb(252, 39, 82); width: 18%; height: 4px;
  position: absolute; top: 14%; left: 41.3%;
}
.Card-wrapper{
  display: flex; justify-content:flex-start; align-items: center;
  width: 38vw; height: 53vh; position: relative; flex-direction: column;
  background-color: #FFFFFF; border-radius: 5px; padding: 20px;
  font-family: Poppins, sans-serif;
}
.Card-wrapper:hover{
  box-shadow: rgb(203, 202, 202) 0px 0px 15px;
}
.image{
  width: 8vw; height: 8vw; aspect-ratio: 1; border-radius: 50%; z-index: 1;
}
.top-div{
  position: absolute; top: -4rem; left: 2rem;
}
.name-div{
  font-size: 25px; font-weight: bold; 
}
.position-div{ color:#757474; font-size: 16px; margin-top: -25px;}
.angle-icon{ color: #e24afc; font-size: x-large; margin-top: 10px;}
.angle-icon2{ color: #e24afc; font-size: x-large; }
.text-div{ color: #b5b3b3; }
button { all: unset; cursor: pointer;}
.angle-dir{ color: #e86bfe; width: 20px; height: 20px; font-size: 23px;
  display: flex;
  justify-content: center; align-items: center; 
}
.btn-div{ display: flex; gap: 15px; position: absolute; top: 67%;}
.surprise-btn{
  background-color: rgb(237, 72, 237); padding: 8px 15px; border-radius: 4px; 
  font-size: 20px; margin-top: 8px; position: absolute; top: 70%; left: 45.5%;
   color: #FFFFFF;
}
.surprise-btn:hover{
  background-color: rgb(187, 55, 187);
}
.exclusive{
  display: flex; justify-content: center; align-items: center;
  flex-direction: column; 
}
@media screen and (max-width: 465px){
  .components{ justify-content: space-between;}
  .Card-wrapper{ width: 77vw; height: 59vh; justify-content: flex-start;}
  .image{ width: 25vw; height: 25vw;}
  .top-div{ top:-3rem; left: 1rem;}
  .name-div{ font-size: 21px; font-weight: bold; }
  .position-div{ color:#757474; font-size: 16px; margin-top: -25px;}
  .line-below{ top: 17%; left: 15.3%; width: 70%;}
  .text-div{font-size: 15px;}
  .angle-icon{ font-size:medium; }
  .angle-icon2{ font-size: medium; }
  .btn-div{ top: 75%;}
  .surprise-btn{ top:78%; left: 34.3%;}
  .exclusive{justify-content: space-between;}
}
@media screen and (max-width: 415px){
  .surprise-btn{ top:78%; left: 32.3%;}
}
@media screen and (max-width: 375px){
  .surprise-btn{ top:78%; left: 30.3%;}
}